.casdastroConta{
    width: 30%;
    margin-left: 35%;
    margin-top: 5%;
    padding: 10px;
    color: #fff;
    border: 2px solid black;
    box-shadow: rgb(38, 57, 77) 0px 20px 30px -10px;
    border-radius: 3px;
}

@media only screen and (max-width: 420px) {
    .casdastroConta{
        width: 75%;
        margin-left: 10%;
        margin-top: 5%;
        padding: 10px;
        color: #fff;
        border: 2px solid black;
        box-shadow: rgb(38, 57, 77) 0px 20px 30px -10px;
        border-radius: 3px;
    }
}