.card{
    width: 33%;
    margin-right: 10%;
    margin-left: 10%;
    margin-bottom: 20px;
    padding: 5px;
    box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
}

@media screen and (max-width: 600px) {

    .card{
        width: 75%;
        margin-right: 10%;
        margin-left: 10%;
        margin-bottom: 20px;
        padding: 5px;
        box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
    }
}